import DistributionAdMonitorCore from './api-client';
import { queue } from './utils/queue';
import { setOptOut } from './utils/optOut';
import pkg from '../package.json';

DistributionAdMonitorCore.helpers = DistributionAdMonitorCore.helpers || {};
DistributionAdMonitorCore.prototype.observers = DistributionAdMonitorCore.observers || {};
// Install internal queue
DistributionAdMonitorCore.on('client', function(client){
  client.extensions = {
    events: [],
    collections: {}
  };
  
  if (!client.config.respectDoNotTrack) {
    this.doNotTrack = false;
  }

  if (typeof client.config.optOut !== 'undefined') {
    setOptOut(client.config.optOut);
    this.optedOut = client.config.optOut;
  }
  
  client.queue = queue(client.config.queue);
  client.queue.on('flush', function(){
    client.recordDeferredEvents();
  });
});


DistributionAdMonitorCore.prototype.referrerPolicy = function(str){
  if (!arguments.length) return this.config.referrerPolicy;
  this.config.referrerPolicy = (str ? String(str) : null);
  return this;
};

DistributionAdMonitorCore.version = pkg.version;

export default DistributionAdMonitorCore;
